.projects-container {
    background-color: #1c1d21;
    margin-top: 75px;
    margin-bottom: 75px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    font-family: 'Roboto Condensed', sans-serif;
}
.project-box {
	margin-top: 25px;
	-ms-flex-preferred-size: 375px;
	    flex-basis: 375px;
	height: 325px;
	border-radius: 5px 5px 0px 0px;
	-webkit-box-shadow: 3px 2px 17px 0px rgba(50, 50, 50, 0.3);
	        box-shadow: 3px 2px 17px 0px rgba(50, 50, 50, 0.3);
	background-color: #31353D;
	overflow: hidden;
}
.skill-container {
	width: 375px;
	height: 215px;
	position: absolute;
	z-index: 10;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	overflow: hidden;
}
.skill {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-item-align: center;
	    align-self: center;
	margin: 10px;
	z-index: 10;
	cursor: default;
	background-color: #1C1D21;
	width: 100px;
	height: 30px;
	border-radius: 5px;
	color: #92CDCF;
	opacity: 0;
	-webkit-transform: translateX(-400px);
	    -ms-transform: translateX(-400px);
	        transform: translateX(-400px);
	-webkit-transition: all 0.6s 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
	-o-transition: all 0.6s 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
	transition: all 0.6s 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.skill:nth-child(2) {
	-webkit-transform: translateY(-400px);
	    -ms-transform: translateY(-400px);
	        transform: translateY(-400px);
}
.skill:nth-child(3) {
	-webkit-transform: translateX(400px);
	    -ms-transform: translateX(400px);
	        transform: translateX(400px);
}
.skill:nth-child(5) {
	-webkit-transform: translateY(400px);
	    -ms-transform: translateY(400px);
	        transform: translateY(400px);
}
.skill:nth-child(6) {
	-webkit-transform: translateX(400px);
	    -ms-transform: translateX(400px);
	        transform: translateX(400px);
}
.project-box img {
	width: 100%;
	height: 275px;
	background-color: #EEEFF7;
	margin: 0px;
	padding: 0px;
	-webkit-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
}
.project-card-footer {
	font-family: 'Roboto Mono', monospace;
	font-size: 15px;
    letter-spacing: 2px;
	width: 100%;
	height: 125px;
	background-color: #31353D;
	-ms-flex-item-align: center;
	    align-self: center;
	color: #bdc3c7;
	-webkit-transition: all 600ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
	-o-transition: all 600ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
	transition: all 600ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.project-card-footer p {
	margin: 0px;
	padding-left: 15px;
	padding-top: 15px;
}
.project-card-footer p:nth-child(2) {
	font-family: 'Roboto Mono', monospace;
	font-size: 12px;
	margin-top: 0px;
}
.project-box:hover .project-card-footer {
	-webkit-transform: translateY(-65px);
	    -ms-transform: translateY(-65px);
	        transform: translateY(-65px);
}
.project-box:hover img {
	-webkit-filter: grayscale(70%);
	        filter: grayscale(70%);
}
.project-box:hover .skill {
	opacity: 0.9;
	-webkit-transform: translateX(0px);
	    -ms-transform: translateX(0px);
	        transform: translateX(0px);
}



