    transition: all 0.4s ease;
    position: relative;
}
.smoke {
    background-color: rgb(99, 99, 99);
    opacity: 0;
    width: 25px;
    height: 25px;
    border-radius: 100px;
    position: absolute;
    left: 125px;
    z-index: 0;
    -webkit-transform: translateY(-20px);
        -ms-transform: translateY(-20px);
            transform: translateY(-20px);
}
.smokeOne {
    -webkit-animation: smokingOne 3.5s ease-out infinite;
            animation: smokingOne 3.5s ease-out infinite;
}
.smokeTwo {
    left: 160px !important;
    -webkit-animation: smokingTwo 3.5s 0.5s ease-out infinite;
            animation: smokingTwo 3.5s 0.5s ease-out infinite;
}
.smokeThree {
    left: 185px !important;
    -webkit-animation: smokingThree 3s 0.2s ease-out infinite;
            animation: smokingThree 3s 0.2s ease-out infinite;
}
.smokeFour {
    left: 90px !important;
    -webkit-animation: smokingFour 5s 0.2s ease-out infinite;
            animation: smokingFour 5s 0.2s ease-out infinite;
}
@-webkit-keyframes smokingOne {
    from { opacity: 0.8; -webkit-transform: scale(1) translateY(-25px); transform: scale(1) translateY(-25px); }
    to { opacity: 0.0; -webkit-transform: scale(1.4) translateY(-55px); transform: scale(1.4) translateY(-55px); }
}
@keyframes smokingOne {
    from { opacity: 0.8; -webkit-transform: scale(1) translateY(-25px); transform: scale(1) translateY(-25px); }
    to { opacity: 0.0; -webkit-transform: scale(1.4) translateY(-55px); transform: scale(1.4) translateY(-55px); }
}
@-webkit-keyframes smokingTwo{
    from { opacity: 0.8; -webkit-transform: scale(1) translateY(-25px); transform: scale(1) translateY(-25px); }
    to { opacity: 0.0; -webkit-transform: scale(1.6) translateY(-45px); transform: scale(1.6) translateY(-45px); }
}
@keyframes smokingTwo{
    from { opacity: 0.8; -webkit-transform: scale(1) translateY(-25px); transform: scale(1) translateY(-25px); }
    to { opacity: 0.0; -webkit-transform: scale(1.6) translateY(-45px); transform: scale(1.6) translateY(-45px); }
}
@-webkit-keyframes smokingThree {
    from { opacity: 0.8; -webkit-transform: scale(1) translateY(-25px); transform: scale(1) translateY(-25px); }
    to { opacity: 0.0; -webkit-transform: scale(2) translateY(-45px); transform: scale(2) translateY(-45px); }
}
@keyframes smokingThree {
    from { opacity: 0.8; -webkit-transform: scale(1) translateY(-25px); transform: scale(1) translateY(-25px); }
    to { opacity: 0.0; -webkit-transform: scale(2) translateY(-45px); transform: scale(2) translateY(-45px); }
}
@-webkit-keyframes smokingFour {
    from { opacity: 0.6; -webkit-transform: scale(1) translateY(-25px); transform: scale(1) translateY(-25px); }
    to { opacity: 0.0; -webkit-transform: scale(2.5) translateY(-45px) translateX(15px); transform: scale(2.5) translateY(-45px) translateX(15px); }
}
@keyframes smokingFour {
    from { opacity: 0.6; -webkit-transform: scale(1) translateY(-25px); transform: scale(1) translateY(-25px); }
    to { opacity: 0.0; -webkit-transform: scale(2.5) translateY(-45px) translateX(15px); transform: scale(2.5) translateY(-45px) translateX(15px); }
}




